* {
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: #222;
}
#quote-box {
  background-color: #ddd;
  color: #222;
  margin: 25vh auto 0 auto;
  padding: 2rem;
  width: 500px;
  display: flex;
  flex-direction: column;
  box-shadow: 0px 0px 40px black;
}
#text {
  font-size: 40px;
  text-align: center;
}

#author {
  margin-left: 60%;
  font-size: 20px;
}

#new-quote {
  display: inline;
  padding: 1rem;
  width: 50%;
  background-color: #ddd;
  border: none;
  box-shadow: 0px 0px 10px black;
}
#new-quote:hover {
  background-color: #eee;
  cursor: pointer;
  border: 2px solid white;
  box-shadow: 0px 0px 5px black;
}
#tweet-wrapper {
  margin-top: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;

  
  

}

a {
  display: block;
}
svg:hover {
  height: 30px;
  width: 30px;
}

#dev-info {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 30px; /* Height of the footer */
  justify-content: center;
  align-items: center;
  display: flex;
  color: white;
  background-color: #0081c9;
}

footer > * {
  margin-right: 20px;
}
footer a {
  display: inline;
  color: white;
  text-decoration: none;
  font-weight: bold;
}

footer img {
  display: inline;
  background: #fff;
  height: 20px;
  width: auto;
  border-radius: 5px;
  margin-right: 5px;
}

#footer-img-wrapper {
  display: flex;
}

@media only screen and (max-width: 600px) {
  #quote-box{
    width: 200px;
    height: 200px;
  }
}

